import React, { useState } from 'react';
import bigInt from "big-integer";
import { bigintToHex } from 'bigint-conversion'
import './App.css';


function App() {
  const [input1, setInput1] = useState('');
  const [input2, setInput2] = useState('');
  const [hexCode, setHexCode] = useState('00000');

  function handleInput1Change(e) {
    setInput1(e.target.value);
  }

  function handleInput2Change(e) {
    setInput2(e.target.value);
  }

  function handleConvert() {
    let decimal = bigInt(input1 + input2);
    let hexadecimal = bigintToHex(decimal);
    setHexCode(hexadecimal.toUpperCase());
  }

  return (
    <div className="container">
      <h1>Conversor <span>SEA</span></h1>
      <div className="container-input">
        <label>
          Site Code:
          <input type="text" value={input1} className="input-style" onChange={handleInput1Change} placeholder="Ingrese Site Code" />
        </label>
        <br />
        <label>
          Card Code:
          <input type="text" value={input2} className="input-style" onChange={handleInput2Change} placeholder="Ingrese Card Code" />
        </label>
        <div className='container-hexa'>
          <span className='hexa'>Hexa Code</span>
          <span className='hexacode'>{hexCode}</span>
          <button className='btn-convert' onClick={handleConvert}>Convertir</button>
        </div>
      </div>
      <footer>Made by <a href='http://github.com/glespinola' target="_blank" rel="noreferrer">glespinola</a></footer>
    </div>
  );
}

export default App;
